import onemessage from './oneMessage'

var socketTask;
var socketOpen = false;

export default {
	components: {
		onemessage,
	},
	data() {
		return {

			chatContent: [{
				messageId: '0001',
				messageType: '1',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				messageId: '0002',
				messageType: '1',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				messageId: '0003',
				messageType: '1',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				messageId: '0004',
				messageType: '0',
				messageTime: '11:29',
				messageStatus: '1'
			}, {
				messageId: '0006',
				messageType: '1',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				messageId: '0006',
				messageType: '0',
				messageTime: '11:29',
				messageStatus: '1'
			}],

			socketMsgQueue: [], // 未发送的消息队列
			textMsg: '', // 输入框中的text
		}
	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "XXXX（聊天人的昵称）"
		});
		
		this.socketInit();
	},
	onHide() {
		// socketTask.close();
	},
	
	methods: {
		socketInit(){
			var that = this;
			// 创建websocket长连接
			socketTask = uni.connectSocket({
				url: 'ws://localhost:8088/ws',
				complete: () => {}
			});
			socketTask.onOpen(function(res) {
				console.log('WebSocket连接已打开！');
				socketOpen = true;
				for (var i = 0; i < that.socketMsgQueue.length; i++) {
					console.log(that.socketMsgQueue[i]);
					socketTask.send({
						data: that.socketMsgQueue[i]
					});
				}
				that.socketMsgQueue = [];
			});
			
			socketTask.onError(function(res) {
				console.log('WebSocket连接打开失败，请检查！');
			});
			socketTask.onMessage(function(res) {
				console.log('收到服务器内容：' + res.data);
			});
			socketTask.onClose(function(res) {
				console.log('WebSocket 已关闭！');
				// 三秒一次重连
				// console.log("重连中..");
				// socketOpen = false;
				// setTimeout(function(){
				// 	that.socketInit();
				// },3000);
			});
			
		},
		
		sendText() {
			if(!this.textMsg){
				return;
			}
			var that = this;
			
			if (socketOpen == true) {
				console.log("1=:"+socketOpen)
				socketTask.send({
					data: that.textMsg
				});
				that.textMsg = '';//清空输入框
			} else {
				console.log("2=:"+socketOpen)
				that.socketMsgQueue.push(this.textMsg);
				that.textMsg = '';//清空输入框
				console.log(that.socketMsgQueue);
				
			}
		}
	}
}
